nav{
    position:fixed;
    top:0;
    width: 100%;
    min-height: 60px;
    padding: 20px 0;
    background: midnightblue;
}

.liste{
    list-style-type: none;
    background: midnightblue;
    height:auto;
    display: flex;
    justify-content: center;
    align-items: center;
    position:relative;
    font-size: 22px;
}

.liste a{
    text-decoration: none;
    color:#f1f1f1;
}

.item{
    margin:0 15px;
    color:#f1f1f1;
    cursor: pointer;
    text-transform: uppercase;
}

.btn{
    background:transparent;
    border: none;
    display: none;
    position: absolute;
    right: 20px;
    top: 20px;
    color: #000;
    font-size: 18px;
}

.fa-bars{
    color:#f1f1f1;
}

@media screen and (max-width:500px){
    nav{
        padding-bottom: 0;
    }

    .liste{
        flex-direction: column;
    }

    .item:nth-child(1){
        margin-top: 40px;
    }

    .item{
        height: 50px;
        width: 100%;
        border-top:1px solid #fff;
        line-height: 50px;
        text-align: center;
    }

    .btn{
        display: block;
    }
}